    monospace;
}

.navbar .navbar-brand .nav-link{color:rgba(255,255,255,0.5)}.navbar .navbar-brand .nav-link.active{color:white}.navbar .nav-item .nav-link{font-size:1.1rem}.navbar .dropdown-menu.show{background-color:#343a40 !important}.navbar .dropdown-menu .nav-link{padding:10px}.navbar .dropdown-menu .nav-link.active{background-color:#666 !important}

