.standard-card-img{background-image:url("https://via.placeholder.com/1600x900");background-size:cover}.card{width:100%;background-position:center;background-size:cover;border:none;border-radius:0px}@media (min-width: 1200px){.card .item{padding:5rem 20rem !important}}.card .item{text-shadow:0px 2px #000000}.card .sky-gradient{background:linear-gradient(140deg, rgba(3,195,17,0.25098), rgba(0,82,183,0.368627)) !important}.card h1{font-size:3.2em}

hr{margin-top:1rem;margin-bottom:1rem;border:0;border-top:1px solid rgba(0,0,0,0.1)}

.carditem:nth-of-type(1) .card .card-title{color:#a94bff}.carditem:nth-of-type(1) .card .card-btn{background:#a94bff}.carditem:nth-of-type(2) .card .card-title{color:#ff5a5a}.carditem:nth-of-type(2) .card .card-btn{background:#ff5a5a}.rgba-dark{background-color:rgba(0,0,0,0.6) !important}

button{outline:0 !important;border:2px solid black}.board-row:not(:last-of-type) button{border-bottom:0}.board-row button:not(:last-of-type){border-right:0}

.board-wrapper{width:100%;padding-top:100%;position:relative}.board-wrapper .board{position:absolute;top:0;left:0;bottom:0;right:0}.board-wrapper .board .board-row{height:100%}.board-wrapper .board .board-row .square{align-items:center;display:inline-grid;justify-content:center;height:100%;font-size:3vw}.board-wrapper .board .board-row .square .square-content{display:flex;align-items:center}.board-wrapper .board .board-row .square.disabled{cursor:auto;background:lightgrey}

.modal{display:table !important;position:absolute;top:0;left:0;height:100%;width:100%}.modal .modal-dialog{display:table-cell;vertical-align:middle}.modal .modal-dialog .modal-content{margin:0 auto;max-width:400px}@media (min-width: 576px){.modal .modal-dialog .modal-content{max-width:500px}}

i.fa{display:inline-block;border-radius:60px;padding:0.5em 0.6em}i.linkedin-bg{background-color:#0E76A8}i.github-bg{background-color:#211F1F}

.pageContainer{position:relative;min-height:100vh}.contentWrapper{padding-bottom:6rem}

